footer {
    width: 100%;
    background: var(--grey);
    height: 100%;
}

.form-box {
    height: 60%;
    /* background: var(--grey-light); */
    justify-items: center;
}

.footer {
    text-align: center;
    grid-template-rows: max-content auto;
    height: inherit;
}

.input-box {
    padding: 10px;
}

.sociaux ul {
    gap: 20px;
}

input,
textarea {
    background: var(--grey-light-medium);
}

.input-box input,
textarea {
    color: white;
    outline: none;
    padding: 2px 10px;
    font-size: 1.2em;
}

.input-box input:focus,
textarea:focus {
    background: white;
    color: black;
}

h3 {
    color: var(--or);
}

.contact-title,
.input-box:focus-within label,
input[type="submit"]:hover {
    color: var(--or);
}

.input-box label,
input[type="submit"] {
    font-size: 1.2em;
}

input[type="submit"] {
    padding: 5px 10px;
    cursor: pointer;
    margin-top: 10px;
}

input[type="submit"]:hover {
    border: var(--or) solid;
}

@media (max-width: 600px) {
    .contact-title {
        color: var(--grey-dark);
    }

    .contact:focus-within .contact-title {
        color: var(--or);
    }
}